//we pick all the elements inside of the body to translate
const elements = document.querySelectorAll("body *");

//array where the ids will be placed
let translate = [];

//Put the ids inside the array
elements.forEach((element) => {
  if (
    element.id &&
    element.id !== "paso-1" &&
    element.id !== "paso-2" &&
    element.id !== "paso-3" &&
    element.id !== "anterior" &&
    element.id !== "siguiente"
  ) {
    translate = [...translate, element.id];
  }
});

//translate elements by changing the content
translate.forEach((translation) => {
  if (window.location.href.includes("#eng")) {
    document.querySelectorAll(`#${translation}`).forEach((entrie) => {
      entrie.textContent = lang.eng[`${translation}`];
    });
  } else if (window.location.href.includes("#fr")) {
    document.querySelectorAll(`#${translation}`).forEach((entrie) => {
      entrie.textContent = lang.fr[`${translation}`];
    });
  } else {
    document.querySelectorAll(`#${translation}`).forEach((entrie) => {
      entrie.textContent = lang.es[`${translation}`];
    });
  }
});

//Apply the reload once the link is clicked
dataReload.forEach((data) => {
  data.onclick = function () {
    setTimeout(() => {
      window.location.reload();
    }, 500);
  };
});
