/**Dropdown */

const icon = document.querySelector(".icon-tabler-world");

icon.addEventListener("click", () => {
  const menu = document.querySelector(".languages");

  if (menu.classList.contains("visual")) {
    menu.classList.remove("visual");
  } else {
    menu.classList.add("visual");
  }
});

/**Dark mode toggle */

document.addEventListener("DOMContentLoaded", function(){
  darkMode();
})

function darkMode(){
  const toggle = document.querySelector(".switch");

  const darkModePreference = window.matchMedia('(prefers-color-scheme: dark)');

  if(darkModePreference.matches){
    document.body.classList.add('dark-theme');
    icon.setAttribute("stroke", "#ffffff");
    e.target.checked = true;
  }else{
    document.body.classList.add('light-theme');
    icon.setAttribute("stroke", "#000000");
    e.target.checked = false;
  }


  darkModePreference.addEventListener("change", function(){
   if(darkModePreference.matches){ 
      document.body.classList.add('dark-theme');
      icon.setAttribute("stroke", "#ffffff");
      e.target.checked = true;
    }else{ 
      document.body.classList.add('dark-theme');
      icon.setAttribute("stroke", "#000000");
      e.target.checked = false;
    }
  });

  toggle.addEventListener("change", (e) => {
    if (e.target.checked) {
      document.body.classList.add('dark-theme');   
      icon.setAttribute("stroke", "#ffffff");
      e.target.checked = true;
    } else { 
      document.body.classList.add('dark-theme');
      icon.setAttribute("stroke", "#000000");
      e.target.checked = false;
    }
  });

}


